.ot-app-wrapper{
    height: 100vh;
    overflow-x: hidden;
    padding-top: 60px;
    background: #e9e9e9;
}
.ot-notifications-wrapper{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    pointer-events: none;
    background: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.notif {
    position: relative;
    background-color: #323232;
    padding: 0.8em 1em;
    vertical-align: middle;
    color: #ddd;
    box-shadow: 0 7px 18px rgba(0,0,0,0.2);
    border-radius: 3px;
    margin: 0.6em 0;
    pointer-events: auto;
    max-width: 400px;
    line-height: 1.5em;
  }

  .notif strong{
      color: #fff;
  }
  
  .show {
    -webkit-animation: show 0.5s;
            animation: show 0.5s;
  }
  
  @-webkit-keyframes show {
    from { -webkit-transform: translateY(-30%); transform: translateY(-30%) }
    to { -webkit-transform: translateY(0); transform: translateY(0) }
  }
  
  @keyframes show {
    from { -webkit-transform: translateY(-30%); transform: translateY(-30%) }
    to { -webkit-transform: translateY(0); transform: translateY(0) }
  }
.ot-login-page{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
}

.ot-login-form{
    background: #fff;
    right: 0;
    margin: auto;
    z-index: 2;
    width: 420px;
    padding: 4.5em 3em;
    border-radius: 10px;
    box-shadow: 5px 10px 30px rgba(6, 5, 48, 0.14);
}

.ot-login-form .ot-login-title{
    font-size: 1.6em;
    margin-bottom: 1em;
}

.ot-login-form .ot-login-title img{
    margin-right: 0.5em;
}

.ot-input-box{
    position: relative;
    margin-bottom: 1.6em;
 }

.ot-input-box input,
.ot-input-box select{
    position: relative;
    display: block;
    background: #f0f0f0;
    width: 100%;
    font-size: 1em;
    padding: 0.6em 0.5em;
    padding-top: 1.6em;
    border: none;
    outline: none;
    border-bottom: 2px solid #999;
    transition: all 0.35s ease-out;

    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.ot-input-box select{
    border-radius: 0;
    background: #f0f0f0 url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100' height='100' fill='%23999'><polygon points='0,0 100,0 50,50'/></svg>") no-repeat scroll 96.5% 60%;
    ;
    background-size: 12px;
}

.ot-input-box input:-webkit-autofill{
    background: #f0f0f0;
}

input:-webkit-autofill {
    transition: background-color 0.1s ease-in-out 0s;
}

.ot-input-box input:focus,
.ot-input-box input.valid,
.ot-input-box input:-webkit-autofill,
.ot-input-box select:focus,
.ot-input-box select.valid {
    border-bottom-color: var(--primary-color);
}

.ot-input-box label {
    position: absolute;
    left: 0.5em;
    top: 0;
    bottom: 0;
    margin: auto;
    height: 16px;
    transition: -webkit-transform 0.15s ease-out;
    transition: transform 0.15s ease-out;
    transition: transform 0.15s ease-out, -webkit-transform 0.15s ease-out;
    pointer-events: none;
    opacity: 0.5;
}

.ot-input-box input:focus + label,
.ot-input-box input.valid + label{
    opacity: 1;
    -webkit-transform: scale(0.7) translate3d(-1em, -1em, 0);
            transform: scale(0.7) translate3d(-1em, -1em, 0)
}

.ot-input-box select:focus+label,
.ot-input-box select.valid+label {
    opacity: 1;
    -webkit-transform: scale(0.7) translate3d(-1.3em, -1em, 0);
            transform: scale(0.7) translate3d(-1.3em, -1em, 0)
}

.ot-login-form p:last-child{
    margin-top: 2em;
}

.ot-btn{
    margin: 0;
    outline: none;
}

@media all and (max-width: 540px){
    .ot-login-form{
        width: 100vw !important;
        padding: 2.5em !important;
        border-radius: 0 !important;
        box-shadow: none !important;
    }
}
.ot-header{
    position: fixed;
    top: 0;
    width: 100vw;
    height: 60px;
    background: #fff;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.16);
    z-index: 5;
    padding: 0 1.5em;
}

.ot-header .ot-logo{
    height: 40px;
}

.ot-header-title{
    font-size: 1.4em;
}

@media screen and (max-width: 540px){
    .ot-header{
        padding: 0 0.5em;
    }
}
.ot-post-list{
    max-width: 660px;
    margin: 2em auto;
}

.ot-post-list .ot-post-list-item:not(:last-child){
    margin-bottom: 1em;
}

.ot-post-item{
    margin-bottom: 1.5em;
}
.ot-post-item{
    background: #fff;
    min-height: 80px;
    overflow: hidden;
    border-radius: 3px;
    border: 1px solid #e6e6e6;
}

.ot-post-item-title{
    padding: 0.5em 1em;
    margin-top: 0.5em;
}

.ot-post-item-title .ot-dp{
    margin-right: 0.8em;
}

.ot-post-item-title .ot-post-item-owner{
    font-weight: bold;
    display: block;
    margin-bottom: 0.06em;
    text-transform: capitalize;
}

.ot-post-item-title .ot-follow-btn{
    margin-left: 0.9em;
}

.ot-post-item-date{
    font-size: 0.85em;
    color: #555;
}

.ot-post-item-content{
    margin: 0.5em 1.2em;
    margin-bottom: 1em;
    font-size: 1em;
    line-height: 1.6em;
    color: #333;
}

.ot-post-options-btn{
    margin-right: 0;
    transition: 0.3s ease-out;
}

.ot-post-options-btn svg{
    fill: #656565;
}

.ot-post-item:not(:hover) .ot-post-options-btn{
    opacity: 0;
    transition: none;
}

.ot-post-reactions{
    padding: 0.5em 1.2em;
    margin-bottom: 0.7em;
    display: flex;
    align-items: center;
    color: #5a5a5a;
}

.ot-post-item-content + .ot-post-reactions{
    margin-top: -0.5em;
}

.ot-post-media + .ot-post-reactions{
    justify-content: center;
}

.ot-post-reactions small.can-click{
    cursor: pointer;
}

.ot-post-reactions small.can-click:hover{
    text-decoration: underline;
    -webkit-text-decoration-color: #a0a0a0;
            text-decoration-color: #a0a0a0;
}

.ot-post-reactions small{
    word-spacing: 0.2em;
    font-size: 0.8em;
    /* font-weight: 100; */
    display: inline-block;
}

.ot-post-reactions span{
    height: 12px;
    width: 1px;
    background: #ccc;
    display: inline-block;
    margin: 0 0.8em;
    margin-bottom: -0.065em;
}

.ot-post-reactions span:last-child{
    margin-right: 0;
}

@media screen and (max-width: 540px){
    .ot-post-item-title {
        padding: 0.5em;
        margin-top: 0.5em;
    }
}
.ot-post-media{
    margin-bottom: 0.8em;
}

.ot-post-images{
    position: relative;
    /* cursor: pointer; */
}

.ot-post-images .ot-post-image{
    min-height: 200px;
    background: #ddd;
    position: relative;
}

.ot-post-images img{
    width: 100%;
    max-height: 450px;
    object-fit: cover;
    object-position: top;
}

.ot-post-images.two-image-grid,
.ot-post-images.three-image-grid{
    display: flex;
    flex-wrap: wrap;
    margin: 0 -0.1em;
}

.ot-post-images.three-image-grid:before,
.ot-post-images.three-image-grid:after{
    content: '';
    display: flex;
    align-items: center;
    justify-content: center;
    letter-spacing: 0.05em;
    position: absolute;
    right: 0.08em;
    bottom: 0.16em;
    height: 200px;
    width: calc(50% - 0.16em);
    z-index: 1;
    background: rgba(0, 0, 0, 0.7);
    opacity: 0.5;
    transition: opacity 0.2s ease-out;
}

.ot-post-images.three-image-grid:after{
    content: '+ ' attr(extras) ' more';
    font-size: 0.9em;
    color: #fff;
    left: 50%;
    top: 50%;
    margin: auto;
    width: 70px;
    height: 30px;
    letter-spacing: 0.07em;
    z-index: 1;
    border-bottom: 1px solid #fff;
    cursor: pointer;
    opacity: 1;
}

.ot-post-images.three-image-grid:hover:before{
    opacity: 1;
}


.ot-post-images.two-image-grid .ot-post-image,
.ot-post-images.three-image-grid .ot-post-image{
    width: calc(50% - 0.16em);
    margin: 0.08em;
}

.ot-post-images.three-image-grid img{
    height: 200px;
}

.ot-post-video{
    position: relative;
    background: #ddd;
}

.ot-post-video.for-youtube{
    padding-bottom: calc(900% / 16);
}

.ot-post-video iframe{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.ot-post-video video{
    width: 100%;
}
.ot-post-comments{
    padding: 0.8em 1.2em;
    background: #f2f4f3;
}

.ot-comments-loader{
    display: block;
    padding: 0.7em 0.2em;
    font-size: 0.9em;
    text-align: center;
    color: #8d8e8e;
}

.ot-post-comment{
    padding: 0.4em 0;
    padding-right: 2em;
    position: relative;
    transition: opacity 0.35s ease-out;
}

.ot-post-comment.temp{
    opacity: 0.6;
}

.ot-post-comment + .ot-post-comment{
    margin-top: 0.5em;
}

.ot-post-comment .ot-dp{
    margin-right: 0.7em;
}

.ot-post-comment p{
    font-size: 0.9em;
    line-height: 1.5em;
    color: #4a4a4a;
    margin-top: 0.3em;
}

.ot-post-comment p strong{
    color: #2f2f2f;
}

.ot-comment-liker{
    position: absolute;
    top: 0.7em;
    right: -8px;
}

.ot-btn.action.ot-comment-liker svg{
    width: 15px;
    height: 15px;
}

@media screen and (max-width: 540px){
    .ot-post-comments {
        padding: 0.8em 1.1em;
        padding-left: 0.7em;
        background: #f2f4f3;
    }
}
.ot-post-commenter{
    padding: 0.5em 1.2em;
    padding-left: 0.9em;
    border-top: 1px solid #f1f1f1;
}

.ot-post-commenter .ot-dp{
    margin-right: 0.7em;
}

.ot-post-commenter input{
    font-size: 1em;
    padding: 0.3em 0;
    border: none;
    outline: none;
}

.ot-post-commenter input::-webkit-input-placeholder{
    color: #aaa;
}

.ot-post-commenter input::-ms-input-placeholder{
    color: #aaa;
}

.ot-post-commenter input::placeholder{
    color: #aaa;
}

.ot-post-commenter-actions{
    position: relative;
    margin-right: -10px;
}

.ot-post-commenter .ot-btn.action{
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    margin: auto 0;
}

.ot-post-commenter-actions .ot-btn.flat,
.ot-post-commenter-actions .ot-btn.action{
    outline: none;
}

.ot-post-commenter-actions.can-like .ot-btn.flat,
.ot-post-commenter-actions:not(.can-like) .ot-btn.action{
    opacity: 0;
    pointer-events: none;
}

@media screen and (max-width: 500px){
    .ot-post-commenter {
        padding: 0.5em 1.1em;
        padding-left: 0.7em;
    }
}
.ot-new-post{
    position: relative;
    max-width: 660px;
    margin: 2em auto;
    margin-bottom: -0.5em;
    background: #fff;
    border-radius: 3px;
    border: 1px solid #e6e6e6;
}

.ot-new-post-button-wrapper{
    padding: 0.4em 1em;
}

.ot-new-post-button-wrapper:not(.visible){
    display: none;
}

.ot-new-post.dragging{
    /* min-height: 300px; */
    pointer-events: none;
}

.ot-new-post::after{
    content: 'DROP FILES HERE';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.75);
    border: 4px dashed #555;
    z-index: 1;
    transition: opacity 0.25s ease-out;
}

.ot-new-post:not(.dragging)::after{
    opacity: 0;
    pointer-events: none;
}

.ot-new-post-wrapper{
    padding: 0.7em;
    position: relative;
}

.ot-new-post-text{
    width: 100%;
    padding: 0.5em;
    font-family: 'OpenSans', sans-serif;
    font-size: 1.1em;
    resize: none !important;
    background: transparent;
    border: none;
    outline: none;
}

.ot-new-post-images{
    padding: 0.3em;
}

.ot-new-post.dragging .ot-new-post-media{
    min-height: 135px;
}

.ot-new-post-image{
    background-color: #ccd6dd;
    border: 1px solid #e6ecf0;
    border-radius: 6px;
    box-shadow: inset 0 1px 0 rgba(0,0,0,0.05), 0 1px 0 rgba(255,255,255,0.5);
    width: 130px;
    height: 130px;
    position: relative;
    margin: 0.3em;
}

.ot-new-post-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.ot-new-post-image:after{
    content: 'LOADING...';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.92);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
    font-size: 0.85em;
    letter-spacing: 0.05em;
}

.ot-new-post-image:not(.loading):not(.is-temp):after{
    opacity: 0;
    pointer-events: none;
}

.ot-new-post-image.is-temp:not(.loading):after{
    content: 'UPLOADING...';
    color: var(--primary-color);
}

.ot-new-post-image .ot-btn.action{
    position: absolute;
    top: 0.2em;
    right: 0.2em;
    background: #000;
    width: 22px;
    height: 22px;
}

.ot-new-post-image .ot-btn.action svg{
    fill: #fff;
    width: 14px;
    height: 14px;
}

.ot-post-list-wrapper:not(.scrolled) .ot-post-creator-fab{
    /* transform: scale(0); */
    /* pointer-events: none; */
}

#newPostCamera{
    position: fixed;
    top: 0; left: 0;
    bottom: 0; right: 0;
    background-color: black;
    z-index: 1000
}

#newPostCamera > div:not(#captureContainer){
    position: relative;
    height: calc(100 * var(--vh));
    display: flex;
    flex-direction: column-reverse
}

#newPostCamera button{
    outline: none;
    -webkit-tap-highlight-color: transparent;
}

#newPostCamera #captureContainer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 1em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 1;
    background: rgba(0,0,0,0.7);
}

#newPostCamera #captureButton{
    background-color: #fff;
    border-radius: 50%;
    height: 56px;
    width: 56px;
    color: #000;
    margin-left: -2em
}

#newPostCamera #captureImage,
#newPostCamera video{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
}

#newPostCamera #captureImage.hidden{
    opacity: 0;
    pointer-events: none;
}

#newPostCamera > #captureContainer{
    margin-top: 0;
    position: fixed;
    bottom: 0;
}

#newPostCamera .ot-btn{
    color: #fff;
}

#newPostCamera .ot-btn.action{
    width: 34px;
    height: 34px;
}

#newPostCamera .ot-btn.action svg{
    width: 30px;
    height: 30px;
    fill: #fff;
}

